

@import "reset.css";


/* ------------------------------------------------

		1. Default stylesheets

------------------------------------------------ */

	html{
		overflow-x: hidden;
	}

	

	.menu_opened{
		left: -150px;
	}

	::-moz-selection{
		background: #cdcdcd;
	}

	::selection{
		background: #cdcdcd;
	}

	::-moz-selection{
		background: #cdcdcd;
	}

	::-webkit-scrollbar{
		background: #cdcdcd;	
		width: 10px;
	}

	::-webkit-scrollbar-thumb{
		background: #181818;
	}

	::-webkit-input-placeholder{
		color:#505050;

		-webkit-transition: text-indent .5s ease, color .5s ease;
				-o-transition: text-indent .5s ease, color .5s ease;
				transition: text-indent .5s ease, color .5s ease;
	}

	input::-moz-placeholder{
		color:#505050;
		opacity:1;
	}

	textarea::-moz-placeholder{
		color: #505050;
		opacity: 1;
	}

	::ms-input-placeholder{
		color:#505050;
	}

	[placeholder]:focus::-webkit-input-placeholder{
		text-indent:10em;
		color:transparent;
	}

	

	

	

/* ------------------------------------------------

		2. General Classes

------------------------------------------------ */

	.align_left{
		text-align: left;
	}

	.align_right{
		text-align: right;
	}

	.align_center{
		text-align: center;
	}

	.alignleft{
		float: left;
	}

	.alignright{
		float: right;
	}

	img.alignleft{
		margin-right: 20px;
		margin-bottom: 5px;
	}

	img.alignright{
		margin-left: 20px;
		margin-bottom: 5px;
	}

	img.aligncenter,
	.iframe_wrap.aligncenter{
		margin-bottom: 13px;
	}

	img[class*="align"] + p{
		padding-top: 4px;
	}

	blockquote[class*="align"]{
		width: 450px;
	}

	blockquote.alignright{
		margin-left: 20px;
	}

	blockquote.alignleft{
		margin-right: 20px;
	}

	.page_title{
		margin-bottom: 39px;
	}

	.section_offset:not(:last-child){
		margin-bottom: 40px;
	}

	.detailed{
		color: #0097ad;
	}

	.j_hide{
		display: none;
	}

	#page_wrap{
		padding: 156px 0 100px;
		overflow: hidden;
	}

	.extended_wrap + #page_wrap{
		padding: 56px 0 100px;
	}

	#page_wrap.wall{
		padding: 109px 0 100px;
	}

	#page_wrap.ribbon{
		padding-top: 109px;
	}

	#page_wrap.ribbon{
		padding-bottom: 19px !important;
	}

	#page_wrap.flow{
		padding-top: 0px !important;
		padding-bottom: 0px !important;
	}
	
	#page_wrap.fullscreen{
		padding: 0;
	}

	.template_container{
		padding-left: 30px;
		padding-right: 30px;
	}

	.iframe_wrap{
		position: relative;
		height: 0px;
		padding-bottom: 58%;
	}

	.iframe_wrap > iframe{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	iframe[src*="soundcloud.com/player"]{
		width: 100%;
	}

	.on_the_sides{
		width: 100%;
		display: table;
		table-layout: fixed;
	}

	.on_the_sides > *{
		display: table-cell;
		vertical-align: middle;
	}

	.on_the_sides > .right_side:last-child{
		text-align: right;
	}

	.on_the_sides > .right_side:last-child *{
		text-align: left;
	}

	.on_the_sides [class*="_side"] > *{
		display: inline-block;
	}

	.xy_centered::after{
		content: "";
		display: inline-block;
		vertical-align: middle;
		height: 90%;
	}

	.xy_centered > *{
		display: inline-block;
		vertical-align: middle;
	}

	

	

    

    

