	/* --------------------------------------------
			4.3 Tooltips
	-------------------------------------------- */

		.tooltip_container{
			display: inline-block;
			position: relative;
		}

		.tooltip{
			display: block;
			font-size: 13px;
			color: #181818;
			white-space: nowrap;
			padding: 0 10px;
			position: absolute;
			background: #f8f8f8;
			border: 1px solid #181818;
			visibility: hidden;
			opacity: 0;

			-webkit-transition: -webkit-transform .3s cubic-bezier(.2, 3, 1, 1), visibility .3s ease-out, opacity .3s ease-out;
					-webkit-transition: 		visibility .3s ease-out, opacity .3s ease-out, -webkit-transform .3s cubic-bezier(.2, 3, 1, 1);
					transition: 		visibility .3s ease-out, opacity .3s ease-out, -webkit-transform .3s cubic-bezier(.2, 3, 1, 1);
					-o-transition: 		transform .3s cubic-bezier(.2, 3, 1, 1), visibility .3s ease-out, opacity .3s ease-out;
					transition: 		transform .3s cubic-bezier(.2, 3, 1, 1), visibility .3s ease-out, opacity .3s ease-out;
					transition: 		transform .3s cubic-bezier(.2, 3, 1, 1), visibility .3s ease-out, opacity .3s ease-out, -webkit-transform .3s cubic-bezier(.2, 3, 1, 1);
		}

		.tooltip::after{
			content: "";
			display: block;
			width: 0;
			height: 0;
			position: absolute;
			border-style: solid;
			border-color: transparent;
		}

		.tooltip.top{
			bottom: 100%;
			left: 50%;
			margin-bottom: 7px;

			-webkit-transform: translate(-50%, -10px);
				-ms-transform: translate(-50%, -10px);
					transform: translate(-50%, -10px);
		}

		.tooltip.top::after{
			top: 100%;
			left: 50%;
			margin-left: -4px;
			border-width: 0 0 9px 11px;
			border-left-color: #181818;
		}

		.tooltip.right{
			left: 100%;
			margin-left: 12px;
			top: 50%;

			-webkit-transform: translate(10px, -50%);
				-ms-transform: translate(10px, -50%);
					transform: translate(10px, -50%);
		}

		.tooltip.right::after{
			right: 100%;
			top: 50%;
			margin-top: -6px;
			border-width: 0 0 11px 9px;
			border-bottom-color: #181818;
		}

		.tooltip.left{
			right: 100%;
			margin-right: 12px;
			top: 50%;

			-webkit-transform: translate(-10px, -50%);
				-ms-transform: translate(-10px, -50%);
					transform: translate(-10px, -50%);
		}

		.tooltip.left::after{
			left: 100%;
			top: 50%;
			margin-top: -6px;
			border-width: 0 9px 11px 0;
			border-bottom-color: #181818;
		}

		.tooltip.bottom{
			top: 100%;
			margin-top: 7px;
			left: 50%;

			-webkit-transform: translate(-50%, 10px);
				-ms-transform: translate(-50%, 10px);
					transform: translate(-50%, 10px);
		}

		.tooltip.bottom::after{
			bottom: 100%;
			left: 50%;
			margin-left: -4px;
			border-width: 9px 0 0 11px;
			border-left-color: #181818;
		}

		.tooltip_container:hover .tooltip{
			visibility: visible;
			opacity: 1;
		}

		.tooltip_container:hover .tooltip.top,
		.tooltip_container:hover .tooltip.bottom{
			-webkit-transform: translate(-50%, 0px);
				-ms-transform: translate(-50%, 0px);
					transform: translate(-50%, 0px);
		}

		.tooltip_container:hover .tooltip.right,
		.tooltip_container:hover .tooltip.left{
			-webkit-transform: translate(0px, -50%);
				-ms-transform: translate(0px, -50%);
					transform: translate(0px, -50%);
		}

	/* --------------------------------------------
			4.4 Lists
	-------------------------------------------- */

		[class*="list_style_"]{
			overflow: hidden;
		}

		[class*="list_style_"]:not(:only-child){
			padding-left: 20px;
		}

		[class*="list_style_"]:not(:last-child){
			margin-bottom: 20px;
		}

		.vertical_list > li:not(:last-child),
		[class*="list_style_"] > li:not(:last-child){
			margin-bottom: 12px;
		}

		[class*="list_style_"] > li{
			position: relative;
			padding-left: 20px;
		}

		[class*="list_style_"] > li::before{
			font-family: 'Fontello';
			position: absolute;
			left: 0px;
		}

		.list_style_arrow > li{
			padding-left: 15px;
		}

		.list_style_arrow > li::before{
			content: '\e876';
			font-size: 18px;
		}

		.list_style_check > li::before{
			content: '\e813';
			font-size: 15px;
		}

		.list_style_circle > li{
			padding-left: 17px;
		}

		.list_style_circle > li::before{
			content: '\e932';
			font-size: 9px;
		}

		.list_style_plus > li,
		.list_style_minus > li{
			padding-left: 22px;
		}

		.list_style_plus > li::before{
			content: '\e819';
			font-size: 15px;
		}

		.list_style_minus > li::before{
			content: '\e81c';
			font-size: 15px;
		}

		.list_style_numbered{
			counter-reset: list;
		}

		.list_style_numbered > li{
			counter-increment: list;
		}

		.list_style_numbered > li::before{
			font-family: inherit;
			content: counter(list)'. ';
		}

		.social_links > li{
			float: left;
			margin-bottom: 0px;
		}

		.social_links > li > a{
