		}

		.social_links::after{
			content: "";
			display: block;
			clear: both;
		}

		.payment_type:not(:last-child){
			margin-bottom: 18px;
		}

		.payment_type > li:not(:last-child){
			margin-bottom: 18px;
		}

		.page_actions{
			text-align: right;
			position: relative;
			z-index: 1;
		}

		.page_actions::after{
			content: "";
			display: block;
			clear: both;
		}

		.page_actions > li{
			display: inline-block;
			vertical-align: middle;
		}

