		}

		table td,
		table th{
			padding: 9px 20px 8px;
			border: 1px solid #181818;
		}

		th{
			color: #fff;
			background: #181818;
		}

		th:not(:last-child){
			border-right-color: #505050;
		}

		table.type_2 th:first-child{
			border-bottom-color: #505050;
		}

		table.type_2 > *:last-child > tr:last-child > th:first-child{
			border-bottom-color: #181818;
		}

