			font-size: 16px;
			position: relative;
			top: auto;
			right: auto;
			line-height: inherit;
			height: auto;
			margin-top: 0;
		}

		.product_col{
			padding-top: 10px;
			padding-bottom: 10px;
		}

		.qty_col{
