	/* --------------------------------------------
			5.1 Tabs and Tour Sections
	-------------------------------------------- */

		.tabs_nav{
			overflow: hidden;
		}

		.ts_nav{
			float: left;
			width: 170px;
			margin-right: 4px;
		}
		
		.tabs_nav > li{
			float: left;
			margin: 0 4px 4px 0;
		}

		.ts_nav > li{
			margin-bottom: 4px;
		}

		.ts_nav > li > a,
		.tabs_nav > li > a{
			color: #888;
			font-weight: 700;
			cursor: pointer;
			display: block;
			border: 2px solid #ddd;
			min-width: 125px;
			padding: 6px 20px;
			background: #fff;
			overflow: hidden;
			position: relative;
			z-index: 1;
			text-decoration: none !important;

			-webkit-transition: color .5s ease, border-color .5s ease, background-color .5s ease;
					-o-transition: color .5s ease, border-color .5s ease, background-color .5s ease;
					transition: color .5s ease, border-color .5s ease, background-color .5s ease;
		}

		.ts_nav > .active > a,
		.tabs_nav > .active > a{
			color: #fff;
			border-color: #181818;
		}

		.ts_nav > li > a::after,
		.tabs_nav > li > a::after{
			content: "";
			position: absolute;
			top: -2px;
			left: 0;
			width: 100%;
			height: 108%;
			z-index: -1;
			opacity: 0;
			background-color: #181818;

			-webkit-transform: translateY(70%);
					-ms-transform: translateY(70%);
			    transform: translateY(70%);

			-webkit-transition: -webkit-transform .5s ease, opacity .5s ease;
					-webkit-transition: 		opacity .5s ease, -webkit-transform .5s ease;
					transition: 		opacity .5s ease, -webkit-transform .5s ease;
					-o-transition: 		transform .5s ease, opacity .5s ease;
					transition: 		transform .5s ease, opacity .5s ease;
					transition: 		transform .5s ease, opacity .5s ease, -webkit-transform .5s ease;
		}

		.tabs_nav > .active > a::after,
		.ts_nav > .active > a::after{
			opacity: 1;

			-webkit-transform: translateY(0px);
					-ms-transform: translateY(0px);
			    transform: translateY(0px);
		}

		.ts_nav > li > a::before,
		.tabs_nav > li > a::before{
			display: none !important;
		}

		.tab{
			font-size: 14px;
			line-height: 22px;
			overflow: hidden;
			background: #f8f8f8;
			border: 2px solid #181818;
			padding: 24px 20px;
		}

	/* --------------------------------------------
			5.2 Accordion and Toggles
	-------------------------------------------- */

		.toggles > dt,
		.accordion > dt{
			color: #888;
			font-weight: 700;
			cursor: pointer;
			display: block;
			border: 2px solid #ddd;
			background: #fff;
			position: relative;
			padding: 6px 50px 6px 20px;
			margin-bottom: 4px;

			-webkit-transition: color .3s ease, border-color .3s ease, background-color .3s ease;
					-o-transition: color .3s ease, border-color .3s ease, background-color .3s ease;
					transition: color .3s ease, border-color .3s ease, background-color .3s ease;
		}

		.toggles > dt::after,
		.accordion > dt::after{
			content: '\e817';
			font-family: 'Fontello';
			position: absolute;
			right: 20px;
			color: #888;
			height:2px;
			line-height: 0px;
			top: 50%;
			margin-top: -1px;
		}

		.toggles > .active,
		.accordion > .active{
			color: #181818;
			background: #f8f8f8;
			border-color: #181818;
			border-bottom-color: transparent;
			margin-bottom: 2px;
		}

		.toggles > .active::after,
		.accordion > .active::after{
			content: '\e81a';
		}

		.toggles > dd,
		.accordion > dd{
			font-size: 14px;
			line-height: 22px;
			overflow: hidden;
			position: relative;
			top: -4px;
			background: #f8f8f8;
			border-width: 0 2px 2px 2px;
			border-style: solid;
			border-color: #181818;
			padding: 7px 20px 20px;
